<template>
    <div id="home">
        <div class="content mt30">
              <div class="newsList">
                <ul>
                      <li v-for="(val,index) in followData">
                        <a href="conText.html">
                              <h2>{{val.m_title}}</h2>
                              <p>{{val.m_summary}}</p>
                              <p class="writer">
                                  <span>作者：{{val.m_writer_name}}</span>
                              </p>
                          </a>
                      </li>
                  </ul>
              </div>
          </div>
    </div>
</template>
<script>
    export default {
        components : {
           
        },
        data(){
            return {
                followData:[]
            }
        },
        mounted (){
            this.fetchData();
        },
        methods :{
           fetchData(){
              var _this = this;
              this.followData = [];
              this.$http('./data/follow.json').then((res)=>{
                  //console.log(res);
                  _this.followData = res.data.data.list;
              },(errow)=>{
                  alert('请求出错了');
              });
           }
        }
    }
</script>
<style scoped>
    .mt30{
        margin-top:30px;
    }
    .writer{
        padding: 8px 0;
        text-align: right;
    }
</style>


