import {
    SHOWHEADER,
    HIDEHEADER,
    SHOWLOAING,
    HIDELOAING,
    SHOWFOOTER,
    HIDEFOOTER
}
from './types.js'
import getters from './getters.js'


const state = {
    isShowHeader: true,
    isShowLoading: false,
    isShowFooter: true
}

const mutations = {
    [SHOWHEADER](state) {
        state.isShowHeader = true;
    }, [HIDEHEADER](state) {
        state.isShowHeader = false;
    }, [HIDELOAING](state) {
        state.isShowLoading = false;
    }, [SHOWLOAING](state) {
        state.isShowLoading = true;
    }, [HIDEFOOTER](state) {
        state.isShowFooter = false;
    }, [SHOWFOOTER](state) {
        state.isShowFooter = true;
    }
}


export default {
    state,
    mutations,
    getters
}


