<template>
    <div id="home">
        <div class="content">
             <BannerView></BannerView>
              <div class="newsList">
                <ul>
                        <router-link tag="li" v-for="(item,index) in homeData" :to="'/article/' + index">
                           <a href="conText.html">
                              <h2>{{index+1}}--{{item.title}}</h2>
                              <p>{{item.detail}}</p>
                          </a> 
                        </router-link>
                  </ul>
              </div>
          </div>
    </div>
</template>
<script>
    import BannerView from './Banner.vue'
    export default {
        components : {
            BannerView
        },
        data (){
          return {
             homeData : []
          }
        },
        mounted(){
           this.fetchData();
        },
        methods :{
          fetchData (){
              var _this = this;
              this.homeData = [];
              this.$http('./data/index.json').then((res)=>{
                  //console.log(res);
                  _this.homeData = res.data;
              },(errow)=>{
                  alert('请求出错了');
              });
          }
        }
    }
</script>
<style scoped>
    
</style>


