 import * as types from './types.js'
     //console.log(types.HIDEHEADER);
 export default {
     showHeader({
             commit
         }) {
             commit(types.SHOWHEADER);
         },
         hideHeader({
             commit
         }) {
             commit(types.HIDEHEADER);
         },
         showLoading({
             commit
         }) {
             commit(types.SHOWLOAING);
         },
         hideLoading({
             commit
         }) {
             commit(types.HIDELOAING);
         },
         showFooter({
             commit
         }) {
             commit(types.SHOWFOOTER);
         },
         hideFooter({
             commit
         }) {
             commit(types.HIDEFOOTER);
         }
 }


