<template>
    <div id="home">
        <div class="content mt30">
              <div class="newsList">
                <ul>
                       <li v-for="(val,index) in columnData">
                        <a href="conText.html">
                              <h2>{{val.m_title}}</h2>
                              <p>{{val.m_summary}}</p>
                              <p class="writer">
                                  <span>作者：{{val.m_writer_name}}</span>
                              </p>
                          </a>
                      </li>
                  </ul>
              </div>
          </div>
    </div>
</template>
<script>
    export default {
        components : {
           
        },
        data (){
            return {
                columnData : []
            }
        },
        mounted (){
            this.fetchData();
        },
        methods :{
            fetchData (){
                var _this = this;
                this.columnData = [];
                this.$http('./data/column.json').then((res)=>{
                    _this.columnData = res.data.data.list;
                },(error)=>{
                    alert('请求出错了')
                });
            }
        }
    }
</script>
<style scoped>
    .mt30{
        margin-top:30px;
    }
    .writer{
        text-align: right;
        padding: 6px 0;
    }
</style>


